.header {
  min-width: 320px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.header__logo {
  width: 101px;
  height: 25px;
}

@media screen and (min-width: 768px) and (max-width: 1439px) {
  .header__logo {
    width: 125px;
    height: 31px;
  }
}

@media screen and (min-width: 1440px) {
  .header__logo {
    width: 192px;
    height: 48px;
  }
}

.icon-menu-close {
  width: 32px;
  height: 32px;
  fill: currentColor;
}

.header__menu {
  height: 144px;
  justify-content: center;
  align-items: center;
  column-gap: 56px;
  display: none;
}

@media screen and (min-width: 1440px) {
  .header__menu {
    display: flex;
  }
}

.header__menu-item {
  position: relative;
}

.header__menu-link {
  text-align: center;
  letter-spacing: .01em;
  color: #2b2b2b;
  padding-top: 12px;
  padding-bottom: 7px;
  font-family: Caviar Dreams;
  font-size: 18px;
  line-height: 1.16;
}

.drop-down {
  justify-content: space-between;
  align-items: baseline;
  gap: 4px;
  display: flex;
}

.header__icon-menu {
  fill: #008cef;
  width: 10px;
  height: 4px;
  transition-property: transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  transform: rotate(180deg);
}

.header__menu-item:hover .header__icon-menu {
  transform: rotate(0deg);
}

.sub__menu {
  width: 144px;
  height: 114px;
  opacity: 0;
  pointer-events: none;
  border-bottom-left-radius: 72px;
  border-bottom-right-radius: 72px;
  padding-top: 37px;
  font-size: 16px;
  transition-property: opacity, transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -5px);
  box-shadow: 20px 20px 80px rgba(0, 0, 0, .1);
}

.header__menu-item:hover .sub__menu, .header__menu-item:focus .sub__menu {
  opacity: 1;
  pointer-events: auto;
  transform: translate(-50%);
}

.header__sub-menu-link {
  text-align: center;
  letter-spacing: .01em;
  color: #2b2b2b;
  padding-top: 4px;
  font-family: Caviar Dreams;
  font-size: 16px;
  line-height: 1.18;
}

.adaptive-content {
  justify-content: right;
  align-items: center;
  display: flex;
}

.lang-button {
  cursor: pointer;
  letter-spacing: .01em;
