.menu-toggle {
  cursor: pointer;
  color: #487996;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 1440px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle--close {
  left: 60%;
}

@media screen and (min-width: 1440px) {
  .menu-toggle--close {
    display: none;
  }
}

.icon-menu-close {
  width: 32px;
  height: 32px;
  fill: currentColor;
}

.phone-button {
  width: 24px;
  height: 24px;
  cursor: pointer;
  color: #008cef;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  margin-right: 24px;
  padding: 0;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

@media screen and (min-width: 768px) {
  .phone-button {
    display: none;
  }
}

.phone-icon {
  fill: currentColor;
}

